import React, { Component } from 'react';
import axios from 'axios';

class Register extends Component {
  constructor(props) {
    super(props);
    this.state={
      email: null,  
      password: null,
      passwordC: null,

      eVal: false, //  
      pVal: false, //  
      ppVal: false //   
    }
  }

  handleChange = (e) => {  
    //   
    if(e.target.placeholder === "email"){
      if(e.target.value.match(/^([\w-]+(?:\.[\w-]+)*)@((?:[\w-]+\.)*\w[\w-]{0,66})\.([a-z]{2,6}(?:\.[a-z]{2})?)$/)){
        this.setState({ email: e.target.value, eVal: true })        
      } else{
        this.setState({ email: e.target.value, eVal: false })
      }
    }

    //    (( ),, ~!@#$%^&*()-_?  )
    else if(e.target.placeholder === ""){
      if(e.target.value.match(/^(?=.*[a-zA-Z])(?=.*[!@#$%^*+=-])(?=.*[0-9]).{8,16}$/)
        && (e.target.value.length >= 8) && (e.target.value.length <= 16)) {
        this.setState({ password: e.target.value, pVal: true })
      } else{
        this.setState({ password: e.target.value, pVal: false })
      }
    }

    //  
    else if(e.target.placeholder === " "){
      if(this.state.password === e.target.value) {
        this.setState({ passwordC: e.target.value, ppVal: true })
      } else{
        this.setState({ passwordC: e.target.value, ppVal: false })
      }
    }    
  }  

  handleAuth = () => {
    axios.post( 
      'Register', 
      'auth='+true+'&email='+this.state.email, 
      { 'Content-Type': 'application/x-www-form-urlencoded' }
    )
    alert('    .\n(  1~2    .)')
  }

  handleRegister = (e) => {
    //   
    if(this.state.eVal && this.state.pVal && this.state.ppVal){
      axios.post( 
        'Register', 
        'auth='+false+
        '&email='+this.state.email+
        '&password='+this.state.password+
        '&key='+document.getElementById('key').value, 
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      .then( response => {
        if(response.data === 'dupError') alert('  .')
        else if(response.data === 'authError') alert(' .')
        else if(response.data === 'complete') {
          alert(' .')
          window.location = "/";
        }
        else alert(response.data)
      }) 
      .catch( response => { console.log('err\n'+response); } ); // ERROR
       
    }
    else{
      alert(' ')
    }
  }

  render() {
    const { password, passwordC, pVal, ppVal } = this.state
    console.log(password, pVal)
    return (
      <div>
        <br/>
        <input placeholder="email" onChange={this.handleChange}/>
        <button onClick={this.handleAuth}> </button><br/>
        {!this.state.eVal && <text>   .<br/></text>}<br/>
         <br/>
        <input placeholder="" id="key" onChange={this.handleChange}/><br/><br/>

         ("( ), ,   8~16")<br/>
        <input type="password" placeholder="" onChange={this.handleChange}/><br/>
         <br/>
        <input type="password" placeholder=" " id="passwordC" onChange={this.handleChange}/><br/>
        {((password !== '') && (password !== null) && !pVal) && <text>   .<br/></text>}
        {((passwordC !== '') && pVal && !ppVal) && <text> .<br/></text>}

        <button onClick={this.handleRegister}></button>
        {/*       <button onClick={() => window.location = "/"}> </button> */}
      </div>      
    );
  }
}

export default Register;




