import React, { Component } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';

import { login } from '../reducers/logInOut';

class Login extends Component {
  constructor(props) {
    super(props);
    this.state={
      email: null,
      password: null
    }
  }

  handleChange = (e) => {  
    if(e.target.placeholder === "email"){
      this.setState({
        email: e.target.value
      })
    }
    else{
      this.setState({
        password: e.target.value
      })
    }
    
    //   
    if(this.state.email != null && this.state.password !=null){
      this.setState({
        isVal: false
      })
    }
  }

  handleLogin = () => {
    (this.state.email !== null && this.state.password !== null) ?    
      axios.post( 
        'LogInOut', 
        'email='+this.state.email+'&password='+this.state.password, 
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      .then( response => {
        if(response.data === 'emailError') alert('  .')
        else if(response.data === 'pwError') alert('  .')
        else if(response.data === 'complete') {
          this.props.onLogin()
          window.location = "/";
        } 
        else alert(response.data)
      }) 
      .catch( response => { console.log('err\n'+response); } ) : // ERROR
      alert(' ')
  }
  
  //    
  handleLoginT = () => {
    this.props.onLogin()
  }

  render() {
    return (
      <div>
        <input type="text" placeholder="email" name="email" onChange={(e)=>this.handleChange(e)}/><br/>
        <input type="password" placeholder="" name="password" onChange={(e)=>this.handleChange(e)}/><br/>
        <button onClick={this.handleLogin}></button>
        <button onClick={this.handleLoginT}></button><br/><br/>
        <Link to="/register"><button></button></Link>
        <Link to="/findInfo"><button> </button></Link>
      </div>      
    );
  }
}

let mapDispatchToProps = (dispatch) => {
  return {
    onLogin: () => dispatch(login())
  }
}

Login = connect(undefined, mapDispatchToProps)(Login);

export default Login;




