import React, { Component } from 'react';
import axios from 'axios';
import Post from './Post';

class Board extends Component {
  constructor(){
    super();
    this.state={
      post: false,  // true :  , , false :  
      write: false, // true :  , false :   /  

      post_num: 0,  //    
      postList: [], //    10  

      pageNum:1,  //    
      pageNumList: [] //    
    }
  }

  componentWillMount() {
    this.getBoard(1)
  }

  //        10    
  getBoard = (i) => {
    axios.get( 'Board?pageNum='+i )
    .then( response => {
      var pNL = []  // state   
      for(var i = 1; i <= (response.data.count-1)/10+1; i++){
        pNL.push(i)
      }
      this.setState({
        postList: response.data.postList, // 10  
        pageNumList: pNL
      })
    }) 
    .catch( response => { console.log('err\n'+response); } ); // ERROR
  }

  //   state    
  selectPage = (i) => {
    var pn = 1  //    
    if(i > this.state.pageNumList.length){
      pn = this.state.pageNumList.length
      this.setState({
        pageNum: this.state.pageNumList.length
      })
    } else {
      pn = i
      this.setState({
        pageNum: i
      })
    }

    this.getBoard(pn)
  }

  //     
  selectPost = (i) => {
    this.setState({
      post: true,
      post_num: this.state.postList[i].post_num,
      write: false
    })
  }

  //    
  moveList = () => {
    this.setState({
      post: false,
      write: false,
      post_num: 0
    })
    this.getBoard(this.state.pageNum)
  }

  //  
  writePost = () => {
    this.setState({
      post: true,
      write: true,
      post_num: 0
    })
  }

  render() {
    const { post, post_num, write, postList, pageNum, pageNumList} = this.state
    return (
      <div>
        { //  /   ,   
        post
          ? <div><Post post_num={post_num} write={write}/>  
          <button onClick={this.moveList}></button></div>
          : <table>
            <thead><tr>
              <th colSpan="4" className="text-right">  </th>
            </tr><tr>
              <th>No</th><th>Title</th><th>Author</th><th>Date</th>
            </tr></thead>
            <tbody>
              { // state    map   
              postList.map((p, i) => {
                return (<tr key={i}>
                  <td>{p.post_num}</td>
                  <td onClick={() => this.selectPost(i)} style={{cursor:"pointer"}}>{p.title}{p.comment_count !== '0' &&' ('+p.comment_count+')'}</td>
                  <td>{p.email}</td>
                  <td>{p.post_time}</td>
                </tr>)
              })}
              <tr>
                <td colSpan="4">
                  { /*  ,  10   */ }
                  <text onClick={() => this.selectPage(1)} style={{cursor:"pointer"}}>&lt;&lt; </text>
                  {pageNum>10 && <text onClick={() => this.selectPage(parseInt((pageNum-11)/10,10)*10+10)} style={{cursor:"pointer"}}> &lt;</text>}
                  { //     10  
                  pageNumList.slice(parseInt((pageNum-1)/10,10)*10, parseInt((pageNum-1)/10,10)*10+10).map((p, i) => {
                    return(<a key ={i} onClick={() => this.selectPage(p)}>
                      {pageNum === p ? <b style={{cursor:"pointer"}}>  {p}  </b> : <text style={{cursor:"pointer"}}>  {p}  </text>}
                    </a>)
                  })}
                  { /*  10 ,    */ }
                  {parseInt((pageNum-1)/10, 10) < parseInt((pageNumList.length-1)/10, 10) &&
                    <text onClick={() => this.selectPage(parseInt((pageNum+9)/10,10)*10+1)} style={{cursor:"pointer"}}>&gt;</text>}     
                  <text onClick={() => this.selectPage(pageNumList.length)} style={{cursor:"pointer"}}> &gt;&gt;</text>
                </td>
              </tr>
              <tr><td colSpan="4"><button onClick={this.writePost}> </button></td></tr>
            </tbody>
          </table>}
      </div>
    );
  }
}

export default Board;




