import React, { Component } from 'react';
import axios from 'axios';

class Post extends Component {
  constructor(){
    super();
    this.state={
      modify: false,
      post: {},
      comment:[]
    }
  }

  componentWillMount() {
    //  
    if(this.props.write === 'write'){ 
      this.setState({
        modify: false
      })
    }
    else{ //       
      this.setState({
        modify: false,
      })

      //   
      axios.get(
        'Post?post_num='+this.props.post_num,
        { 'Content-Type': 'application/x-www-form-urlencoded' } )
      .then( response => {
        this.setState({
          post: response.data
        })
      }) 
      .catch( response => { console.log('err\n'+response)}); // ERROR
    }
  }

  //     
  componentDidMount() { if(this.props.write !== 'write') this.getComment() }

  //   
  getComment = () => {
    axios.get(
      'Comment?post_num='+this.props.post_num,
      { 'Content-Type': 'application/x-www-form-urlencoded' } )
    .then( response => {
      this.setState({
        comment: response.data
      })
    }) 
    .catch( response => { console.log('err\n'+response)}); // ERROR
  }

  //   
  enrollPost = () => { 
    //  
    if(document.getElementById('title').value === '') {
      alert(' ')
      return
    }
    if(document.getElementById('content').value === '') {
      alert(' ')
      return
    }

    //  
    if(window.confirm(" ?")){
      var now = new Date();
      var post_time = now.getFullYear()+'-'+
        ("0"+(now.getMonth()+1)).slice(-2)+'-'+
        ("0"+now.getDate()).slice(-2)+'T'+
        ("0"+now.getHours()).slice(-2)+':'+
        ("0"+now.getMinutes()).slice(-2)+':'+
        ("0"+now.getSeconds()).slice(-2)+'.000'

      // /    
      var params
      if(this.props.write){
        params = 'action=write'+
          '&title='+document.getElementById('title').value+
          '&content='+document.getElementById('content').value+
          '&post_time='+post_time
      } else if(this.state.modify){ 
        params = 'action=modify'+
          '&post_num='+this.props.post_num+
          '&title='+document.getElementById('title').value+
          '&content='+document.getElementById('content').value
      }

      //  
      axios.post( 
        'Post', params,
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      alert('.')
      
      window.location = "/board"; //      
    }
  }

  //   
  modifyPost = () => { 
    if(window.confirm(" ?")){
      this.setState({
        modify: true
      })
    }
  }

  //   
  handleModify = (e, h) => {
    if(h==='title'){
      this.setState({
        post:{
          title: e.target.value,
          content: this.state.post.content
        }
      })
    } else{
      this.setState({
        post:{
          title: this.state.post.title,
          content: e.target.value
        }
      })
    }
    
  }

  //   
  deletePost = () => {
    if(window.confirm(" ?")){
      axios.post( 
        'Post', 
        'action=delete'+
        '&post_num='+this.props.post_num,
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      alert('.')
      window.location = "/board"; //      
    }
  }

  //   
  enrollComment = () => {
    //  
    if(document.getElementById('comment').value === '') {
      alert(' ')
      return
    }
    
    var now = new Date();
    var comment_time = now.getFullYear()+'-'+
      ("0"+(now.getMonth()+1)).slice(-2)+'-'+
      ("0"+now.getDate()).slice(-2)+'T'+
      ("0"+now.getHours()).slice(-2)+':'+
      ("0"+now.getMinutes()).slice(-2)+':'+
      ("0"+now.getSeconds()).slice(-2)+'.000'

    axios.post( 
      'Comment', 
      'action=enroll'+
      '&post_num='+this.props.post_num+
      '&comment='+document.getElementById('comment').value+
      '&comment_time='+comment_time,
      { 'Content-Type': 'application/x-www-form-urlencoded' }
    )
    .then( response => {
      this.setState({
        comment: response.data
      })
    }) 
    .catch( response => { console.log('err\n'+response)}); // ERROR

    document.getElementById('comment').value = ''
  }

  //   
  deleteComment = (i) => {
    if(this.state.comment.length>0){
      axios.post( 
        'Comment', 
        'action=delete'+
        '&post_num='+this.props.post_num+
        '&comment_time='+this.state.comment[i].comment_time,
        { 'Content-Type': 'application/x-www-form-urlencoded' }
      )
      .then( response => {
        this.setState({
          comment: response.data
        })
      }) 
      .catch( response => { console.log('err\n'+response)}); // ERROR
    }
  }

  render() {
    const { write } = this.props
    const { post, modify, comment} = this.state

    //   -     ========================================================================================== //
    // const write = false
    // const modify = false
    // const post = {"writer":false,"title":"zxbasdfgweqtgw","email":"test","content":"asdgfweeqwrq","post_time":"2018-07-31 18:47:35"}
    // const comment = [{"comment_time":"2018-07-31 18:47:40","comment":"hhhhhhhhhhh","writer":false,"email":"test"}]
    // ================================================================================================================================ //

    return (
      <div>
        {write || modify
        ? <div><input id="title" value={post.title} onChange={(e, h='title') => this.handleModify(e, h)}/></div>
        : <div><h3>{post.title}</h3>
          {post.email} | {post.post_time}</div>
        }
        <textarea id="content" value={post.content} onChange={(e, h='content') => this.handleModify(e, h)} style={{height:500, width:"70%", resize:"none"}} readOnly={!write && !modify}/><br/>
        {(write || modify) && <button onClick={this.enrollPost}></button>}
        {(!write && !modify) && <div>
          {post.writer && <div><button onClick={this.modifyPost}></button><button onClick={this.deletePost}></button></div>}
          <input id="comment" placeholder=" "></input >
          <button onClick={this.enrollComment}> </button>
          { comment.map((c, i) => {              
            return (<div style={{border:"1px solid"}}>
              <b>{c.email}</b>  <small>{c.comment_time}</small> {c.writer && <button onClick={() => this.deleteComment(i)}> </button>}<br/>
              {c.comment}
            </div>)
          })}
        </div>}
      </div>
    );
  }
}

export default Post;




